ile game development for iOS, Android, and Facebook, leveraging AS3/Adobe Air/Flash. Played a pivotal role in the innovative development of games across multiple platforms, ensuring seamless user experiences and broad market reach. </div><div class=\"tech-stack\" data-v-56390e0f> ActionScript 3, Adobe Air, Flash, Starling Framework </div><h2 data-v-56390e0f>Skills</h2>", 23)
